.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s, visibility .5s, transform .5s;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0003;
  justify-content: center;
  align-items: center;
  transition: opacity .5s, visibility .5s, transform .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop .modal {
  width: calc(100vw - 3.9rem);
  height: 40rem;
  text-align: right;
  opacity: 1;
  visibility: 1;
  pointer-events: initial;
  background: #e17992;
  border-radius: 50% 20%;
  transition: transform .5s;
  overflow: scroll;
  transform: scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.backdrop .modal__text {
  color: #fff;
  text-align: center;
  margin: 4rem;
  font-family: DM Sans;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.9rem;
  overflow: hidden;
}

.backdrop .modal .modal-x-button {
  cursor: pointer;
  background: #e17992;
  border: none;
  border-radius: 50%;
  margin-top: 18px;
  margin-right: 18px;
  padding: 0;
}

.backdrop .modal .modal-x-button .modal-x-icon {
  fill: #fff;
  stroke: #fff;
  margin: 6px;
  padding: 3px 3px 4px 4px;
  display: block;
}

